import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

    box: {
        display: "grid",
        gridTemplateColumns:'40% 50%',
        height: "100%",
        background: "transparent",
        padding:'30px 70px',
        '@media(max-width:1385px)':{
            gridTemplateColumns:"38% 44%",
            padding:'31px 40px',
            '@media(max-width:1300px)':{
                gridTemplateColumns:"45% 71%",
                '@media(max-width:1040px)':{
                    display:'flex',
                    flexDirection:'column-reverse   ',
                    '@media(max-width:580px)':{
                        marginTop:'40px',
                        padding:'unset'
                     }
                }
            }
        }
    },
    boxContent1: {
        width: "530px",
        height: "620px",
        textAlign: "center",
        marginLeft: "auto",
        marginRight: "100px",
        '@media(max-width:1300px)':{
            width:'442px',
            marginRight:'51px',

            '@media(max-width:1160px)':{
                marginLeft:'4px',
                '@media(max-width:1106px)':{
                    width:'426px',
                    '@media(max-width:1040px)':{
                    //    width:'100%',
                    //    display:'flex',
                    //    margin:'auto',
                    //    justifyContent:'center'
                    display:'none'
                    }
                }
            }
    
        }

    },
    boxContent2: {
        width: "668px",
        textAlign: "center",
        height: "100%",
        padding: "47px 33px 55px 33px",
        background: "#FFFFFF",
        marginLeft:'100px',

        boxShadow: "0px 0px 100px rgba(0, 0, 0, 0.05)",
        borderRadius: "8px",
        '@media(max-width:1300px)':{
            width:'541px',
            marginLeft:'72px',

            '@media(max-width:1106px)':{
                width:'493px',
                marginLeft:'30px',

                '@media(max-width:1040px)':{
                    width:'100%',
                    display:'flex',
                    flexDirection:'column',
                    margin:'auto',
                    justifyContent:'center'
                 }
            }
    

        }

    },
    img: {
        width: "100%",
        height: "100%",
    },
    h3: {        
        fontWeight: "700",
        fontSize: "24px",
        lineHeight: "33px",
        color: "#1B1C33",
        marginBottom: "24px",
    },
    p: {
        fontSize: "24px",
        lineHeight: "33px",
        textAlign: "center",
        color: "#1B1C33",
        marginBottom: "61px",
        maxWidth: "536px",
    }, 
    baseIput: {
        width: "100%",
        marginBottom: "35px",
    },
    baseInputWrapper: {
        position: "relative",
        paddingTop: "7.72px",
        width: "100%",
    },
    input: {
        width: "100%",
        backgroundColor: "#fff",
        padding: "14px 18px",
        lineHeight: 1,
        fontSize: "15px",
        borderRadius: "8px",
        fontWeight: "400",
        transition: ".2s ease-out",
        border: "1px solid #1B1C33",
        borderRadius: "8px",
        outline: "none",
    },   
    button: {
        width: "392px",
        height: "54px",
        background: "#1B1C33",
        borderRadius: "8px",
        fontWeight: "600",
        fontSize: "16px",
        lineHeight: "22px",
        color: "#FFFFFF",
        '@media(max-width:1040px)':{
            width:'60%',
            margin:'auto',
            '@media(max-width:860px)':{
                width:'100%',
             }
         }
    }
});