// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./Blog2Styles";
import './Blog2Styles.scss'
import {BlogStore} from '../../assets/blog'
import { Link } from "react-router-dom";
import { ROUTES } from "../../constants";
import { useHistory } from "react-router-dom";

export default function Blog2View(props) {
  const router=useHistory();
  let reloadRoute = (r) => {
    
    router.push({ pathname: r });
    window.location.reload()
}
  const checkAge=(idx) =>{
  return idx.slug.toLowerCase() > props.slug.toLowerCase();
}
  let curentIdx=BlogStore.findIndex(checkAge)
  console.log(props)
  return (
    <div className="blog2">
      <div className={css(styles.box)}>
        <div className={css(styles.imgBox)}>
          <img className={css(styles.img)} src={props.img} alt="..." />
        </div>
        <div className={css(styles.contentBox)}>
          <p className={css(styles.date)}>
            <span className={css(styles.span)}>Author’s Name: {props.author}</span>
            <span className={css(styles.span)}>17-02-2022</span>
            <span className={css(styles.span)}>11:51</span>
          </p>
          <div className={css(styles.textBox)}>
            <div className={css(styles.text)} dangerouslySetInnerHTML= {{__html:props.details}}>
            </div>
          </div>
         
        </div>
      </div>
    </div>
  );
}
