import React from "react";
import { Route, BrowserRouter as Router, Switch } from "react-router-dom";
import { connect } from "react-redux";
import { ROUTES } from "../constants";
import {
  HomePage,
  PageNotFound,
  Waitlist,
  Blog,
  BlogDescription,
  Podcast,
  Login,
  Register,
  Portal,
  Portal2,
  Dashboard,
  SuccessStories
} from "../pages";
import LayoutWrapper from "./LayoutWrapper";
import { BrowserRouter } from "react-router-dom";

const NoAuthRoute = ({ ...props }) => {
  return (
    <LayoutWrapper>
      <Route {...props} forceRefresh />
    </LayoutWrapper>
  );
};

class Routers extends React.PureComponent {
  render() {
    const { user } = this.props;
    const repeatedProps = {
      user,
      exact: true,
      forAdminOnly: false,
    };
    return (
      <Router>
        <div>
          <Switch>
            <NoAuthRoute
              path={ROUTES.HOME}
              component={HomePage}
              {...repeatedProps}
            />
          <NoAuthRoute
              path={ROUTES.WAITLIST}
              component={Waitlist}
              {...repeatedProps}
            />

          <NoAuthRoute
              path={ROUTES.BLOG}
              component={Blog}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.BLOG+"/:id"}
              component={BlogDescription}
              {...repeatedProps}
            />

            <NoAuthRoute
              path={ROUTES.BLOGDESCRIPTION}
              component={BlogDescription}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.PORTAL}
              component={Portal}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.PORTAL2}
              component={Portal2}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.DASHBOARD}
              component={Dashboard}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.PODCAST}
              component={Podcast}
              {...repeatedProps}
            />
            <NoAuthRoute
              path={ROUTES.LOGIN}
              component={Login}
              {...repeatedProps}
            /><NoAuthRoute
            path={ROUTES.REGISTER}
            component={Register}
            {...repeatedProps}
          />
          <NoAuthRoute
            path={ROUTES.SUCCESSSTORIES}
            component={SuccessStories}
            {...repeatedProps}
          />

            {/* Keep this in last always */}
            <NoAuthRoute path={Route.PAGE_NOT_FOUND} component={PageNotFound} />
          </Switch>
        </div>
      </Router>
    );
  }
}

const mapStateToProps = ({ user }) => ({
  user,
});

const actions = {};

export default connect(mapStateToProps, actions)(Routers);
