import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

    box: {
        margin: "20px auto",
        width: "392px",
        // height: "298px",
        background: "#FFFFFF",
        //boxShadow: "0px 0px 100px rgba(0, 0, 0, 0.05)",
        borderRadius: "8px",
       
        '@media(max-width:1355px)':{
            width:'450px'
        },
        '@media(max-width:1255px)':{
            width:'400px'
        },
        '@media(max-width:978px)':{
            width:'100%'
        }
    },
    img:{
            width:"389px",
        '@media(max-width:1355px)':{
            width:'450px'
        },
        '@media(max-width:1255px)':{
            width:'400px'
        },
        '@media(max-width:978px)':{
           width:'100%'
        }
    },
    title: {
        fontWeight: "400",
        fontSize: "24px",
        lineHeight: "33px",
        color: "#1B1C33",
        marginTop: "10px",
        marginBottom: "20px",
    },
    text: {
        fontSize: "16px",
        lineHeight: "22px",
        color: "#1B1C33",
    },
    link: {
        fontWeight: "bold",
        color: "red",
        padding: "10px 0"
    },
    contentBox:{
        paddingLeft:"10px"
    }
});