// @flow
import React from "react";
import "./Portal2Styles.scss";
import { WebHeader, WebFooter, Plan, Background, } from "../../components";


export default function Portal2View() {
  return(
    <div className="portal2">
        <Background >
        <WebHeader/>
        <div className="main">
            <Plan
            head="MONTHLY PLAN"
            title="$37 /  month"
            subtitle="BILL MONTHLY"
            color="#5F5F5F"
            headColor="#1B1C33"
            btnText = "Join Monthly"
            />
            <Plan
             head="ANNUAL PLAN"
             title="$32 /  month"
             subtitle="BILL ANNUALLY"
             color="#FFF"
             Bgcolor= "#455582"
            btnText = "Join Annually"
            btnColor = "#fff"
            btn="#1B1C33"
            />
        </div>
        <WebFooter/>
        </Background>
    </div>
  );
}
