import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

    box: {
        margin: "auto",
        maxWidth: "668px",
        height: "636px",
        background: "#FFFFFF",
        boxShadow: "0px 0px 100px rgba(0, 0, 0, 0.05)",
        borderRadius: "8px",
    },
    boxContent: {
        maxWidth: "424px",
        margin: "auto",
        textAlign: "center",
        height: "100%",
    },
    img: {
        paddingTop: "112px",
        marginBottom: "35px",
    },
    h3: {        
        fontWeight: "700",
        fontSize: "24px",
        lineHeight: "33px",
        color: "#1B1C33",
        marginBottom: "24px",
    },
    p: {
        fontSize: "16px",
        lineHeight: "22px",
        textAlign: "center",
        color: "#212121",
        marginBottom: "50px",
    },    
});