// @flow
import React from "react";
import BlogdescrptionView from "./BlogdescrptionView";
import {BlogStore} from "../../assets/blog"
import { Redirect } from "react-router-dom";
import { ROUTES } from "../../constants";
class BlogdescrptionController extends React.Component {
  static propTypes = {};
  constructor(props) {
    super(props)
    this.state = {blogDetails:''}
    this.details=BlogStore.filter((v)=>{
      return this.props.match.params.id===v.slug
    })
    

    
}

componentDidMount() {
  window.scrollTo(0, 0)
  this.setState({
    blogDetails:this.details,
  
  })
}
  render() {
console.log(this.state)
this.props =this.details
    console.log(this.props)
    
    if(this.details){
      return <BlogdescrptionView  {...this.props}/>;
    }else{
      return <Redirect to={ROUTES.BLOG} />
    }
  }
}

export default BlogdescrptionController;
