import { StyleSheet } from "aphrodite";

export default StyleSheet.create({
    footer: {
        background: "#212121",        
        color: "#FFFFFF",
        width: "100%",
        height: "auto",
    },
    footerBox: {
        // display: "flex",
        // justifyContent: "space-around",
        // alignItems: "center",
        display:'grid',
        gridTemplateColumns:'auto auto auto',
        padding: "60px 0 64px 99px",
        '@media(max-width:900px)':{
            gridTemplateColumns:'100%',
            padding:'20px'
        }
    },
    footerContent: {
        maxWidth: "380px",
        '@media(max-width:900px)':{
            maxWidth:'unset',
            width:'90%',
            margin:'20px auto',
            display:'flex',
            flexDirection:'column',
            alignItems:'center'
        }
    },
    media: {
        fontSize: "24px",
        fontWeight: "700",
        lineHeight: "33px",
        color: "#FFFFFF",
        '@media(max-width:900px)':{
           textAlign:'center'
        }
    },
    link: {
        fontSize: "16px",
        lineHeight: "22px",
        color: "#FFFFFF",
        fontWeight: "400",
    },
    squareBox: {
        width: "100%",
    },
    squareImg: {
        width: "100%",
        opacity:0.2
    },
    footnote:{
        fontFamily: 'Open Sans',
        fontStyle: "normal",
        fontWeight: "400",
        fontSize: "16px",
        lineHeight: "22px",
    }

});