// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./WebFooterStyles"
import footerLogo from "../../assets/images/footerLogo.png"
import square from "../../assets/images/square.png"
import { Link } from "react-router-dom";
import { ROUTES } from "../../constants";
export default function WebFooterView(props) {

  return (
    <div>
      <footer className={css(styles.footer)}>
        <div className={css(styles.footerBox)}>
          <div className={css(styles.footerContent)}>
           {/*  <div className={css(styles.footerImg)}>
              <img className={css(styles.img)} src={footerLogo} alt="..." />
            </div> */}
            <div className={css(styles.footnote)}>Currently creating the woman I want to be. Excuse me if I become extremely protective of myself, my time, and energy.</div>
          </div>
          <div className={css(styles.footerContent)}>
            <h4 className={css(styles.media)}>Company</h4>
            <ul className={css(styles.ul)}>
              <li className={css(styles.li)}><Link to={ROUTES.HOME} className={css(styles.link)}>Home</Link></li>
              <li className={css(styles.li)}><Link to="/#about" className={css(styles.link)}>About</Link></li>
              <li className={css(styles.li)}><Link to={ROUTES.PODCAST} className={css(styles.link)}>Podcast</Link></li>
              <li className={css(styles.li)}><Link to={ROUTES.BLOG} className={css(styles.link)}>Blog</Link></li>
              <li className={css(styles.li)}><Link to={ROUTES.WAITLIST} className={css(styles.link)}>Contact</Link></li>
            </ul>
          </div>
          <div className={css(styles.footerContent)}>
            <h4 className={css(styles.media)}>Social Media</h4>
            <ul className={css(styles.ul)}>
              <li className={css(styles.li)}><Link target={"_blank"} to={{pathname:"https://www.facebook.com/JoJoGrahamSimpleBusinessMentor/"}} className={css(styles.link)}>Facebook</Link></li>
              <li className={css(styles.li)}><Link target={"_blank"} to={{pathname:"https://www.instagram.com/jojo_graham/"}}  className={css(styles.link)}>Instagram</Link></li>
              <li className={css(styles.li)}><Link target={"_blank"} to={{pathname:"#"}}  className={css(styles.link)}>Twitter</Link></li>
              <li className={css(styles.li)}><Link target={"_blank"} to={{pathname:"https://www.linkedin.com/in/jojo-graham/"}}  className={css(styles.link)}>LinkedIn</Link></li>
            </ul>
          </div>
        </div>
        <div className={css(styles.squareBox)}>
          <img className={css(styles.squareImg)} src={square}  alt="..." />
        </div>
      </footer>
    </div>
  );
}
