// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./SuccessStoriesStyles"
export default function SuccessStoriesView(props) {
  return (
    <div>
      
      <div className={css(styles.box)}>
        <div className={css(styles.boxContent)}>
          <p className={css(styles.p)}>
            
            {props.text}
          </p>
          <div className={css(styles.blue)}></div>
          <h5 className={css(styles.h5)}> {props.name}</h5>
          <div className={css(styles.text)}>{props.title}</div>
        </div>
        <div className={css(styles.boxContent)}>
          <img src={props.img}  alt="..." className={css(styles.img)}/>
        </div>
      </div>
    </div>
  );
}
