// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./SigninStyles"
import btsw1 from "../../assets/images/btsw1.png"
import { Link } from "react-router-dom";
import { ROUTES } from "../../constants";
export default function SigninView(props) {
  return (
    <div>         
      <div className={css(styles.box)}>
      <img src={btsw1} className={css(styles.img)}  alt="..."/>
        <div className={css(styles.baseIput)}>
          <div className={css(styles.baseInputWrapper)}>
              <input className={css(styles.input)} type="text" />
              <span className="label-text" data-stay-up="true">Email Address</span>
          </div>
        </div>
        <div className={css(styles.baseIput)+ "password"}>
          <div className={css(styles.baseInputWrapper)}>
              <input className={css(styles.input)} type="password" />
              <span className="label-text" data-stay-up="true">Password</span>
          </div>
        </div>
        <div className={css(styles.btnWrapper)}>
          <button className={css(styles.button)}>Signin</button>
          <p className={css(styles.p)}>
            <Link to={ROUTES.REGISTER} className={css(styles.link)}>Don’t have an account? Signup</Link>
          </p>
        </div>        
      </div>
    </div>
  );
}
