// @flow
import React, { useState } from "react";
import styles from './SStoriesStyles'
import { css } from "aphrodite";
import { WebHeader, WebFooter, Background,SuccessStories,Backdrop,SideDrawer} from "../../components";
import Anna from "../../assets/images/Anna.png"
import Nathasha from "../../assets/images/Natasha.png"
import Selina from "../../assets/images/Selina.png"
import Amy from "../../assets/images/Amy.png"
import Virginia from "../../assets/images/Virginia.png"
import Benny from "../../assets/images/Benny.png"
import Zelica from "../../assets/images/Zelica.png"
import Michelle from "../../assets/images/Michelle.png"
import Tabassum from "../../assets/images/Tabassum.png"
import Rachel from "../../assets/images/Rachel.png"
import Rahana from "../../assets/images/Rahana.png"
import Emma from "../../assets/images/Emma.png"
import Caroline from "../../assets/images/Caroline.png"
import Anna2 from "../../assets/images/Anna2.png"
import Claire from "../../assets/images/Claire.png"
import Melanie from "../../assets/images/Melanie.png"

export default function StoriesView(props) {
  console.log(props)
  const pokes = props.state
  const[sideToggle,setSideToggle] = useState(false)
  return (
    <div className="home">
        <Background>
            <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
        <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
            <h1 id="#about" className={css(styles.boxHeader)}>
              <span >Success Stories</span>
              <span className={css(styles.underline)}></span>
            </h1>
        

            
            {
               pokes.pokemonList.map(poke => {
                   return (<SuccessStories

                    text={poke.text}
        
                    name={poke.name}
        
                    title={poke.title}
        
                    img={poke.img}
                    />)
               }
               )
           }
           <div style={{display:"flex",justifyContent:"center"}}>
           <button className={css(styles.btnregister)} style={pokes.loadMoreButton? {}:{display:"none"}} onClick={()=>props.loadMore()}>Load More</button>
        
           </div>
         
        </Background>

        <WebFooter/>
    </div>
  );
}
