// @flow
import React, { useState } from "react";
import { css } from "aphrodite";
import styles from "./PodcastStyles";
import { Link } from "react-router-dom/cjs/react-router-dom.min";
import Jojo from '../../assets/images/Jojo/Jojo.png'
import Box from '../../assets/images/Box/Box.png'
import Star2 from '../../assets/images/Star2/Star2.png'
import Star from '../../assets/images/Star/Star.png'
import Simple from '../../assets/images/Simple/Simple.png'
import Spotify from "../../assets/images/Spotify/Spotify.png";
import Google from "../../assets/images/Google/Google.png";
import Itunes from "../../assets/images/Itunes/Itunes.png";
import { WebHeader, WebFooter, Podcast,Background,Meet ,Backdrop,SideDrawer} from "../../components";
import { ROUTESEXTERNAL } from "../../constants";
export default function PodcastView(props) {
  const[sideToggle, setSideToggle] = useState(false)
  return (
    <div className="home">
     
        <Background height="70vh">
        <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
        <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/> 
        <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <div className={css(styles.body)}>
          <Podcast/>
        </div>
        </Background>
        <div className={css(styles.box)}>
            <img src={Box} alt ="" className={css(styles.boxImg)}/>
            <div className={css(styles.box2)}>
            <p className={css(styles.text)}>
            JoJo has a wonderful way of making the complex simple. Her podcasts are packed full of<br/> 
            valuable content that you can apply to your business immediately.
            Highly recommend tuning in!” 
            <span className={css(styles.amy)}> Amy Rowl</span>
            <img src={Star2} alt ="" className={css(styles.img2)}/>
            </p>
            
            </div>
        </div>
        <Background height="unset">
        <h1 className={css(styles.h1)}>Meet The Host</h1>
        </Background>
        <Meet>
          <div className={css(styles.grid)}>
            <div className={css(styles.grid1)}>
            <img src={Jojo} alt="" className={css(styles.img3)}/>
            </div>
          <div className={css(styles.p)}>
          <div className={css(styles.repo)}>
          <p className={css(styles.p1)}>
          I’m JoJo and I LOVE supporting entrepreneurs and business owners just like you build, grow and scale their business. I wanted to make sure that learning was as accessible as possible to everyone, which is why The Simple Way Podcast was born!



          <p className={css(styles.p2)}>
          Join me every week where I will share with you Simple yet POWERFUL nuggets of business gold to support you in the growth of your business, as well as growing within yourself as an entrepreneur too.
          </p>
          </p>
         

          </div>
          </div>
          </div>
        </Meet>
        <Background>
          <div className={css(styles.div3)}>
          “There are many business podcasts out there and so many good ones. This one is OUTSTANDING! JoJo gives such simple yet deeply insightful actionable support, inspiring genuinely grounded presence and accessible motivation. I highly recommend this, her as a mentor and this podcast as a way of getting to know her.”
          <p className={css(styles.span)}>Lucy Power</p>      
          <img src={Star} alt="" className={css(styles.star)} />
          </div>
          <h2 className={css(styles.h2)}>Listen Now!!</h2>
          <p className={css(styles.p5)}>
          A new episode is released every week. Tune in wherever you are and make sure to subscribe,
        rate and review if you love it!
          </p>
          <div className={css(styles.imgContainer)}>
          <a href={ ROUTESEXTERNAL.SPORTIFYLINK} target="_blank">
            <button  className={css(styles.button)}>Listen on Spotify</button>
          </a>
          <a href={ROUTESEXTERNAL.SPORTIFYLINK} target="_blank">
          <button  className={css(styles.button)}>Listen on Google</button>
          </a>
          <a href={ROUTESEXTERNAL.APPLELINK} target="_blank">
          <button  className={css(styles.button)}>Listen on Itunes</button>
          </a>
          
        </div>
        <div className={css(styles.div)}>
          <img src={Simple} alt="" className={css(styles.divImg)}/> 
        </div>
        <div className={css(styles.div2)}>
        <button className={css(styles.btn)}>
          <a href={ROUTESEXTERNAL.IGLINK} target="_blank">
          <span className={css(styles.btntext)}>
          Follow us on Instagram
          </span>
          </a>
        </button>
        </div>
        </Background>
        <WebFooter/>
    </div>
  );
}
