// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./Podcast2Styles"
import phone from "../../assets/images/phone.png";
import star from "../../assets/images/star.png"
export default function Podcast2View(props) {
  return (
    <div>
      <div className={css(styles.box)}>
        <img className={css(styles.phone)} src={phone} />
        <div className={css(styles.textBox)}>
          <p className={css(styles.text)}>
            JoJo has a wonderful way of making the complex simple. Her podcasts are packed full of
            valuable content that you can apply to your business immediately.
            Highly recommend tuning in!”
          </p>
          <p className={css(styles.author)}>Amy Rowl</p>
          <div className={css(styles.starBox)}>
            <img className={css(styles.star)} src={star} />
            <img className={css(styles.star)} src={star} />
            <img className={css(styles.star)} src={star} />
            <img className={css(styles.star)} src={star} />
          </div>
        </div>
      </div>
    </div>
  );
}
