// @flow
import React from "react";
import LoginView from "./LoginView";

class LoginControllers extends React.Component {
  static propTypes = {};
  constructor(props) {
    super(props)
}

componentDidMount() {
  window.scrollTo(0, 0)
}
  render() {
    return <LoginView {...this.props} />;
  }
}

export default LoginControllers;
