// @flow
import React, { useState } from "react";
import { css } from "aphrodite";
import styles from "./BlogStyles";
import "./BlogStyles.scss";
import {BlogStore} from "../../assets/blog"
import {Blog ,Background,WebHeader,WebFooter,Backdrop,SideDrawer}from '../../components'
// import Waitlist from '../../assets/images/Waitlist/Waitlist.png'
export default function WaitlistView() {
  const[sideToggle,setSideToggle] = useState(false)
  console.log(BlogStore)
  return (
    <div className=" blog">
        <Background bg="#fff" > 
        <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
        <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <div className={css(styles.body)}>
          {
           BlogStore.map((blog,idx)=><Blog key={idx} data={blog} title={blog.title} img={blog.img} detail={blog.details}/>) 
          }
          
        </div>
        </Background>
        <WebFooter/>
    </div>
  );
}
