// @flow
import React from "react";
import RegisterView from "./RegisterView";

class RegisterControllers extends React.Component {
  static propTypes = {};
  constructor(props) {
    super(props)
}

componentDidMount() {
  window.scrollTo(0, 0)
}
  render() {
    return <RegisterView {...this.props} />;
  }
}

export default RegisterControllers;
