// @flow
import React from "react";
import {css} from "aphrodite";
import styles from "./BlogStyles";
import tima2 from "../../assets/images/tima2.png";
import { Link } from "react-router-dom";
import {ROUTES} from '../../constants/index'

export default function BlogView(props) {
  return (
    <div>
      
      <div className={css(styles.box)}>

      <Link to={ROUTES.BLOG+"/"+props.data.slug}>  
        <div className={css(styles.imgBox)}>
          <img className={css(styles.img)} src={props.img} alt="..." />
        </div>
          </Link>
        <div className={css(styles.contentBox)}>    
          <h3 className={css(styles.title)}>{props.title}</h3>
          <p>By {props.data.author}</p>
          <p className={css(styles.text)}>
            {props.detail}
            <br /><br/>
            <Link to={ROUTES.BLOG+"/"+props.data.slug} className={css(styles.link)}>Read more...</Link>
          </p>
        </div>
        
      </div>
      
    </div>
  );
}
