// @flow
import React from "react";
import "./SidebarStyles.scss";
import Logo from '../../assets/images/Logo/Logo.png'
import { Link } from "react-router-dom";
export default function SidebarView() {
  return(
    
    <div className="sidebar">
      <div>
      <img src={Logo} alt="" />
      </div>

      <ul>
          <li>
              <Link to="/">
              Start Here
              </Link>
          </li>
          <li className="active">
              <Link to="/">
                My Dashboard
              </Link>
          </li>
          <li>
              <Link to="">
                BTSW Blueprint
              </Link>
          </li>
          <li>
              <Link to="">
                Masterclass
              </Link>
          </li>
      </ul>
    </div>
  );
}
