// @flow
import React, { useState } from "react";
import { css } from "aphrodite";
import styles from "./BlogdescrptionStyles"
import {BlogStore} from "../../assets/blog"
import {Blog2 ,Background,WebHeader,WebFooter,SideDrawer,Backdrop}from '../../components'
// import Waitlist from '../../assets/images/Waitlist/Waitlist.png'
export default function BlogDescriptionView(props) {
const[sideToggle,setSideToggle] = useState(false)
const[blog,setBlog] = useState(props[0])
const changeState=(idx)=>{
  window.scrollTo(0, 0)
  setBlog(BlogStore[idx])
}
const checkAge=(idx) =>{
return idx.slug.toLowerCase() === blog.slug.toLowerCase();
}
let curentIdx=BlogStore.findIndex(checkAge)
console.log(curentIdx)
  return (
    <div className="home">
        <Background bg="#fff" height="10vh"> 
        <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
      <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
      <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <div className={css(styles.body)}>
          <Blog2 author={blog.author} slug={blog.slug} details = {blog.details2} img={blog.img}/>
           <div className={css(styles.buttonBox)}>
            {
              curentIdx>0 &&(
                  <button  onClick={()=>{changeState(curentIdx-1)}} className={css(styles.buttona)}>&lt;&lt; Back</button>
               
              )
            }
            {
              curentIdx<BlogStore.length-1 &&(
                  <button  onClick={()=>{changeState(curentIdx+1)}} style={{marginLeft:"auto"}}className={css(styles.buttona)}> Next  &gt;&gt;</button>
               
              )
            }
            
          </div> 
        </div>
        </Background>
        <WebFooter/>
        
    </div>
  );
}
