// @flow
import React, { useState } from "react";
import { css } from "aphrodite";
import styles from "./HomeStyles"
import "./HomeStyles.scss";
import { INSTAGRAM_ACCESS, ROUTES } from "../../constants";
import Logo2 from '../../assets/images/Logo2/Logo2.png'
import Lady from '../../assets/images/Lady/Lady.png'
import Mock from '../../assets/images/Mock/Mock.png'
import Simple from '../../assets/images/Simple/Simple.png'
import Bar from '../../assets/images/Bar/unnamed.png'
import Unity from '../../assets/images/Unity/Unity.png'
import img from '../../assets/images/img/img.png'
import Freedom from '../../assets/images/Freedom/Freedom.png'
import Attain from '../../assets/images/Attain/Attain.png'
import Justice from '../../assets/images/Justice/Justice.png'
import Team from '../../assets/images/Team/Team.png'
import Ceo from '../../assets/images/Ceo/Ceo.png'
import Member from '../../assets/images/Member/Member.png'
import Checkbox from '../../assets/images/Checkbox/Checkbox.png'
import { Link } from "react-router-dom";
import InstagramFeed  from 'react-ig-feed'
import 'react-ig-feed/dist/index.css'
import { WebHeader, WebFooter, Background,CenterBox,BgImage,Backdrop,SideDrawer, Media} from "../../components";
export default function HomeView(props) {
  const[sideToggle,setSideToggle] = useState(false) 
  if(props?.location?.hash==="#about"){
    document.getElementById('about')?.scrollIntoView() 
  }
  return(
    <div className="home">
     
        <Background height="90vh">
        <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
      <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
      <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <div className={css(styles.body)}>
          <div className={css(styles.firstSection)}>
            <div>
              {/* <p className="custom-unique-font">BUSINESS THE <span>simple</span> WAY</p> */}
               <img src={Logo2} alt="" className={css(styles.logo)}/> 
            </div>
            <div className={css(styles.Text)}>
              Build, Grow and Scale your Business with JoJo Graham
            </div>
            <Link to="#">
            <button className={css(styles.btn)}>
              
                  BECOME A MEMBER
             
              
            </button>
            </Link>
          </div>
          <div className={css(styles.secondSection)}>
            <img src={Lady} alt="" style={{width:476}}  className={css(styles.img)}/>
          </div>
        </div>
        </Background>
        <Background bg="#FFFFFF;" height="100vh" >
        <div className={css(styles.relative,styles.m20)}>
        <CenterBox/> 
        </div>
            <h1 id="about" className={css(styles.boxHeader,styles.M20)}>
              <span >What we do</span>
              <span className={css(styles.underline)}></span>
            </h1>
          <div className={css(styles.flex)}>
            <div className={css(styles.section1)}>
              <div className={css(styles.bas)}> 
              <p className={css(styles.para1)}>
              <span className={css(styles.span)}>At Business The Simple Way we teach you how to build a stable and successful business where we focus on 7 key areas:</span>

              <span className={css(styles.span)}>This is JoJo Graham's unique business system that will support you in every area of your business, from magnetic branding, attractive marketing, desirable offerings, sales strategies that feel good, scalability, sustainability and how to step into your leadership as the CEO you are.</span>

              <span className={css(styles.span)}>We want you to have such a simple yet successful business that all you have to focus on is your zone of genius.</span>

              </p>
              </div>
             
              <img src={Bar} alt="" className={css(styles.Img2)}/>
              
            </div>

            <div className={css(styles.section2)}>
              <div className={css(styles.bas)}>
              <img src={Team} alt="" className={css(styles.Img)}/>
              </div>
            
              <div className={css(styles.bas2)}>
                <h1 className={css(styles.head)}>You’re in the right place if you…</h1>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>Are tired of piecing together a strategy from freebies and podcasts that are all full of little nuggets that don’t come together.</span>
                </div>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>Haven’t got a strategy or a plan of action so you are winging it all.</span>
                </div>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>Have no support or mentorship from anyone who has been there and one it with the business expertise to match.</span>
                </div>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>Want to be a part of something that enriches you with knowledge as well as a community that can hold you up whenever things start to feel off.</span>
                </div>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>Need access to Simple business information at the touch of a button 24/7 so you can work on your goals whenever you are inspired</span>
                </div>
                <div className={css(styles.div)}>
                <img src={Checkbox} alt="" />
                <span className={css(styles.checkText)}>It’s time to go to the next level!</span>
                </div>
              </div>
            </div>
          </div>
        </Background>
          <BgImage img={Member} lg="linear-gradient(360deg, rgba(0, 0, 0, 0.61), rgba(0, 0,0, 0.71))"> 
              <p className={css(styles.para2)}>
              <span className={css(styles.center)}>
                 Through the membership, the courses, proven frameworks and mentorship, you will have everything you need to build a business you can be proud of.
              </span>

             <span className={css(styles.center)}>
                Did you know more than 90% of all online businesses end in failure within the first 120 days?!
             </span>

              <span className={css(styles.center)}>
              This stat should not discourage you, BUT it should empower you to be one of the 10% that succeed! The main reason for the 90% is the lack of business knowledge, and this is so easy to fix when you have access to all of the knowledge you need right here!
              </span>

              </p>
              <Link to={ROUTES.WAITLIST}>
              <button className={css(styles.waitbtn)}>
                Join The Waitlist
               
              </button>
              </Link>
          </BgImage>
          <Background height="433px">
            <div className={css(styles.section)}>
              <div className={css(styles.partA)}>
                  <h1 className={css(styles.head2)}>LISTEN TO A PODCAST</h1>
                  <p className={css(styles.para3)}>
                    <span className={css(styles.span)}>Creating a Business and Life you want doesn’t have to be complicated</span>
                    <span className={css(styles.span)}>Straight talking Jojo delivers simple strategies that really work on how to build an Online Business The Simple Way giving you the life you deserve and  desire.</span>
                   <span className={css(styles.span)}> The Simple Way Podcast is a mix of business mentoring, marketing, sales, personal development and life coaching, all rolled into one</span>
                  </p>
              </div>
              <div className={css(styles.partB)}>
                <img src={Simple} alt=""  className={css(styles.Img3)}/>
              </div>
            </div>
          </Background>
          <Background bg="#fff">
          <h1 className={css(styles.boxHeader,styles.beat)}>
              <span >NEVER MISS A BEAT</span>
              <span className={css(styles.underline)}></span>
            </h1>
            <p className={css(styles.center,styles.w300)}>
              Want access to valuable free content and be the first to know what 
              we have got going on? Follow us on Instagram!
            </p>
            <div className={css(styles.w1270)}>
            <InstagramFeed token={INSTAGRAM_ACCESS}  counter="6"/>
            </div>
          </Background>
          <BgImage img={Mock} height="464px"  lg="linear-gradient(270.24deg, #1B1C33 -5.6%, rgba(255, 255, 255, 0.15) 133.84%)">
            <div className={css(styles.content)}>
              <h1 className={css(styles.h2)}>Want to uplevel your content?</h1>
              <p className={css(styles.p2)}>Get the Simple Content Card Deck that gives you 54 content prompts so you never run out of things to say in your marketing!</p>
              <button className={css(styles.btncontent)}>
                <Link>
                  <span className={css(styles.contentText)}>
                  Learn more
                  </span>
                </Link>
              </button>
            </div>
          </BgImage>
          <Background bg="#fff">
          <h1 className={css(styles.boxHeader)}>
              <span >The Founder and CEO</span>
              <span className={css(styles.underline)}></span>
            </h1>
            <div className={css(styles.section3)}>
              <img src={Ceo} alt="" className={css(styles.cImg)} />
              <div className={css(styles.ceo)}>
                <h1 className={css(styles.jojo)}>  JOJO GRAHAM</h1>
                <p className={css(styles.para4)}>
                  <span className={css(styles.span)}>
                JoJo is not only an expert in Business, having a degree in Business Management and a decade of Business experience in Coaching, Mentoring, Consulting, Event Planning and Marketing, she is also a full time Mum.
                  </span>
                  <span className={css(styles.span)}>
                    JoJo has built her success by supporting businesses to grow and scale to 6 figures and beyond through her mentoring. She is also the co-founder of Leaders of Change, a CPD accredited company paving the way for equitable business and conscious leadership. She has generated multiple 6 figures by going all in to support people around the world grow and scale their business using her expertise and leadership. She is now on a mission to inspire and empower as many people as she possibly can to do what they love, create a life of freedom and give them permission to be successful, whatever that means to them. She does this through her podcast, digital courses, content card deck, live events, international retreats and her company Business The Simple Way.

                  </span >
                  <span className={css(styles.span)}>
                  Business isn’t a one size fits all, and this is where so many business owners go wrong. Business The Simple Way gives you all the different tools that you can use for where you are at in your business using a Simple framework.

                  </span>

                </p>
              </div>
            </div>

            <div className={css(styles.relative)}>
            <div className={css(styles.box)}>
              <div >
                <h1 className={css(styles.boxhead)}>About Us</h1>
               <p className={css(styles.boxcontent)}>Business The Simple Way is a company founded by JoJo Graham and specialises in teaching business, marketing, sales and building successful brands.</p>
              </div>
              <div >
              <h1 className={css(styles.boxhead)}>Our Mission</h1>
                <p className={css(styles.boxcontent)}>
              Our mission is Simple, help you make more money and grow sustainable and successful businesses that you love, through Simple strategies that bring ease and joy to your life and business. If you want to get that support, learn how to become a member here!

                </p>
              </div>
            </div>
            </div>
            <h1 className={css(styles.boxHeader2)}>
              <span >Our Values</span>
              <span className={css(styles.underline)}></span>
            </h1>
            <p className={css(styles.center,styles.w301)}>
            Values are the cornerstone to any company and we take ours 
              very seriously as it’s the culture we breed on every level.
            </p>

            <div className={css(styles.grid)}>
              <div className={css(styles.gridBox)}>
                  <img src={Unity} alt="" className={css(styles.gridImage)}/>
                  <div className={css(styles.gridContent)}>
                    <h1 className={css(styles.gridHead)}>Unity</h1>
                    <p className={css(styles.gridText)}>
                    We strongly believe in coming together and connecting in a trusting, safe and respectful 
                    space that welcomes diversity and supports the inclusion of all. When we unite, we can evolve toward a better future. 
                    </p>
                  </div>
              </div>
              <div className={css(styles.gridBox,styles.m150)}>
                  <img src={Freedom} alt="" className={css(styles.gridImage)}/>
                  <div className={css(styles.gridContent)}>
                    <h1 className={css(styles.gridHead)}>Freedom</h1>
                    <p className={css(styles.gridText)}>
                    We believe freedom is our right and supporting individuals in becoming fiercely independent to support themselves and those around 
                    them to have choices to be, have and do what is important to them, is important to us. We believe your freedom is the most important thing in your life 
                    </p>
                  </div>
              </div>

              <div className={css(styles.gridBox)}>
                  <img src={Justice} alt="" className={css(styles.gridImage)}/>
                  <div className={css(styles.gridContent)}>
                    <h1 className={css(styles.gridHead)}>Justice</h1>
                    <p className={css(styles.gridText)}>
                    We care deeply about justice creating fair, respectful and equitable opportunities for all who come into contact with the
                     company. This holds true for our clients, customers, partners and employees. 
                    </p>
                  </div>
              </div>

              <div className={css(styles.gridBox,styles.m200)}>
                  <img src={Attain} alt="" className={css(styles.gridImage)}/>
                  <div className={css(styles.gridContent)}>
                    <h1 className={css(styles.gridHead)}>Attainment</h1>
                    <p className={css(styles.gridText)}>
                    We believe in supporting individuals to attain their goals and ambitions and to step into the greatest versions of themselves.
                     We want everyone to achieve success, whatever that personally means to them and to realise their fullest potential. 
                    </p>
                  </div>
              </div>
            </div>
          </Background>
          <BgImage img={img} height="539px"   lg="linear-gradient(360deg, #1B1C33 -5.6%, rgba(255, 255, 255, 0.15) 133.84%)">
            <p className={css(styles.center,styles.text)}>
            Our goal is to help as many entrepreneurs build, grow and scale the business as possible to whatever success means to them! Are you one of them?
            </p>
            <button className={css(styles.waitbtn)}>
                <Link to={ROUTES.WAITLIST}>
                <span className={css(styles.waittext)}>Join The Waitlist</span>
                </Link>
              </button>
          </BgImage>
          {/* <Media/> */}
        <WebFooter/>
    </div>
  );
}
