// @flow
import React from "react";
import SStoriesView from "./SStoriesView";
import {SStoriesData} from './SStoriesData';
class SStoriesControllers extends React.Component {
  constructor(props){
    super(props); 
    let leges=SStoriesData.filter((value,index,array)=>{
      return 4>index
    });
    this.state = {
        pokemonList: leges,
        loadMoreButton:true,
        position:1
    }   
}


componentDidMount() {
window.scrollTo(0, 0)
}
loadMore=()=>{
  let loadMoreButton=true
  let currentData=[];
  let lege=SStoriesData.filter((value,index,array)=>{
    return ((this.state.position*4)+4)>index
  });
  
  lege.forEach(v=>{
    currentData.push(v);
  })
  if(currentData.length>=SStoriesData.length){
    loadMoreButton=false
  }
  this.setState({pokemonList:lege,loadMoreButton:loadMoreButton,position:(this.state.position + 1)})
  }
  static propTypes = {};
  
  render() {
    
    return <SStoriesView  {...this} />;
  }
}

export default SStoriesControllers;
