import React from 'react'
import { css } from 'aphrodite'
import styles from './Styles'

const index = (props) => {
const bg = {
    background:`${props.lg}, url(${props.img})`,
    minHeight:props.height?props.height:'613px'
}
    
  return (
    <div className={css(styles.body)} style= {bg}>
            {props.children}
    </div>
  )
}

export default index