// @flow
import React from "react";
import Checkbox from '../../assets/images/Checkbox/Checkbox.png'
import  "./PlanStyles.scss"

export default function PlanView(props) {
  return (
    <div className="plan" style={{background:props.Bgcolor,color:props.color}}>
        <h1 className="head" style={{color:props.headColor}}>{props.head}</h1>
        <h3 className="title"  style={{color:props.headColor}}>{props.title}</h3>
        <h4 className="subtitle"  style={{color:props.headColor}}>{props.subtitle}</h4>
        <div className="section">
          <div className="checkboxDiv">
              <img src={Checkbox} alt="" className="img"/>
              <span className="checkText">Access to this months Masterclass.</span>
          </div>
          <div className="checkboxDiv">
              <img src={Checkbox} alt="" className="img"/>
              <span className="checkText">Access to the BossBabe Business Blueprint- your roadmap to 6 figures.</span>
          </div>
          <div className="checkboxDiv">
              <img src={Checkbox} alt="" className="img"/>
              <span className="checkText">Access to the BossBabe Business Blueprint- your roadmap to 6 figures.</span>
          </div>
          <div className="checkboxDiv">
              <img src={Checkbox} alt="" className="img"/>
              <span className="checkText">Invitation to join our secret Société Community</span>
          </div>
          <div className="checkboxDiv">
              <img src={Checkbox} alt="" className="img"/>
              <span className="checkText">Four live group coaching calls via video each month + accountability.</span>
          </div>
          <div className="btn" style={{background:props.btnColor}}>
            <span className="btntext" style={{color:props.btn}}>{props.btnText}</span>
          </div>
        </div>
    </div>
  );
}
