// @flow
import React, { useState } from "react";
import {Waitlist ,Background,WebHeader,SideDrawer,Backdrop}from '../../components'
// import Waitlist from '../../assets/images/Waitlist/Waitlist.png'
export default function WaitlistView() {
  const[sideToggle,setSideToggle] = useState(false)
  return (
    <div className="home">
        <Background>
        <WebHeader show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        
        <SideDrawer show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <Backdrop show={sideToggle}  click={()=>setSideToggle(!sideToggle)}/>
        <Waitlist/>
        </Background>
    </div>
  );
}
