// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./SuccessStyles"
import good from "../../assets/images/good.png"
export default function SuccessView(props) {
  return (
    <div>
      <div className={css(styles.box)}>
        <div className={css(styles.boxContent)}>
          <img className={css(styles.img)} src={good} />
          <h3 className={css(styles.h3)}>Congratulations</h3>
          <p className={css(styles.p)}>
            You have successfully joined the waitlist! 
            Keep an eye on 
            your inbox for an exciting 
            announcement very soon!
          </p>
        </div>
      </div>
    </div>
  );
}
