import React from "react";
import PortalView from "./PortalView";

class PortalController extends React.Component {
  static propTypes = {};
  constructor(props) {
    super(props)
}

componentDidMount() {
  window.scrollTo(0, 0)
}
  render() {
    return <PortalView {...this.props} />;
  }
}

export default PortalController;
