import React from 'react'
import { css } from 'aphrodite'
import styles from './Styles'

const index = () => {
  return (
    <div className={css(styles.body)}>
        <h2 className={css(styles.head)}>Welcome to Business The Simple Way</h2>
        <p className={css(styles.para1)}>
            <span className={css(styles.span1)}> Where we do anything but make things hard! Growing a business shouldn’t have to be complicated, all you need are the right tools and strategies to make your business a success for you.</span>

            <span className={css(styles.span1)}>Whether you are a startup or an established business, it’s time to make it Simple. Why Simple?</span>
            <span className={css(styles.span1)}>When things are made Simple, it creates more ease and flow in your life and business which creates room for more freedom and joy, and that’s what it’s all about right?</span>
            <span className={css(styles.span1)}>Building a business should feel good, and here at Business The Simple Way we will help you do just that.</span>



        </p>
    </div>
  )
}

export default index