import React from 'react'
import './Styles.scss'
import Beauty from '../../assets/images/Beauty.png'
import { Link } from 'react-router-dom'
import { ROUTES } from '../../constants'

const LTR = (props) => {
  return (
    <div className='ltr'>
        <div className="container">
          <img src={Beauty} alt="" className='img1'/>
          <img src={props.img2} alt="" className="img2" />
        </div>
        <div className="section">
          <h1 className="title">{props.title}</h1>
          <h1 className="head">{props.head}</h1>

          <p className="para1">{props.para1}</p>
          <p className="para2">{props.para2}</p> 
          {props.children}
            <button>
                <Link to ={ROUTES.REGISTER}>
                    <span className="register">Click Below to join us right now!</span>
                </Link>
                </button>
        </div>
    </div>
  )
}

export default LTR