// @flow
import React, { useRef, useState } from "react";
import {css} from "aphrodite"
import styles from "./WaitlistStyles"
import tima from "../../assets/images/tima.png";
import emailjs from '@emailjs/browser';
export default function WaitlistView(props) {
  
  const [message,setMessage]=useState('');
  const [name,setName]=useState('');
  const [error,setError]=useState('');
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_lz4gyex', 'template_1ykgtkt', form.current, 'jBLVvLU7HQWsoc183')
      .then((result) => {
        setName("")
        setMessage("")
        setError("Message Sent Successfully")
      }, (error) => {
          console.log(error.text);
      });
  };
  function nameChangeHandler(event){
    setName(event.target.value)
  }
  function messageChangeHandler(event){
    setMessage(event.target.value)
  }
  return (
    <div>
      <div className={css(styles.box)}>
        <div className={css(styles.boxContent1)}>
          <img className={css(styles.img)} src={tima} alt="" />
        </div>
        <form ref={form} onSubmit={sendEmail}>
        <div className={css(styles.boxContent2)}>
          <h3 className={css(styles.h3)}>Join Waitlist</h3>
          <p className={css(styles.p)}>
          Join the waitlist now to be the first to know about the exclusive membership!
          </p>
          
          <div className={css(styles.baseIput)}>
            <p>{error}</p>
            <div className={css(styles.baseInputWrapper)}>
            <input
                  type="text"
                  className={css(styles.input)}
                  placeholder={"Your Name*"}
                  name="from_name"
                  required
                   value={name}
                   onChange={nameChangeHandler}
                />
                <span className="label-text" data-stay-up="true">Name</span>
            </div>
          </div>
          <div className={css(styles.baseIput)}>
            <div className={css(styles.baseInputWrapper)}>
            <textarea
                  placeholder={"Your message*"}
                  className={css(styles.input)} rows={7}
                  name="message"
                  required
                  value={message}
                  onChange={messageChangeHandler}
                ></textarea>
                <span className="label-text" data-stay-up="true">Message</span>
            </div>
          </div>
          <button className={css(styles.button)}>Join The Waitlist Here!</button>
        </div>
        </form>
      </div>
    </div>
  );
}



                

               

                