// @flow
import React from "react";
import {css} from "aphrodite"
import styles from "./PopUpStyles"
import { Link } from "react-router-dom";
export default function PopUpView(props) {
  return (
    <div>
      <div className={css(styles.box)}>
        <div className={css(styles.boxContent)}>
          <h3 className={css(styles.h3)}>JOIN THE WAITLIST</h3>
          <p className={css(styles.p)}>Want to be the first to know how you can be an 
            insider in Business 
            The Simple Way and have exclusive access to
            JoJo and her business trainings?
          </p>
          <button className={css(styles.button)}>Join the waitlist now!</button>
        </div>
      </div>
    </div>
  );
}
