import { StyleSheet } from "aphrodite";

export default StyleSheet.create({
    body: {
        marginTop: '8vh',
        padding: '10px 50px',
        '@media(max-width:500px)':{
            padding:'unset'
        },
    },
    h1:{
    fontFamily:'Playfair Display',
    fontStyle:'normal',
    fontWeight:700,
    fontSize:'32px',
    lineHeight:'43px',
    color:'#1B1C33',
    textAlign:'center',
    padding:'60px 0'
    },
    h2:{
    fontFamily:'Playfair Display',
    fontStyle:'normal',
    fontWeight:700,
    fontSize:'64px',
    lineHeight:'86px',
    color:'#1B1C33',
    textAlign:'center',
    padding:'0px 0px 38px 0px',
    '@media(max-width:900px)':{
        lineHeight:'40px',
        fontSize:'50px'
    }

    },
    grid:{
     display:'grid',
     gridTemplateColumns:'35% 65%',
     height:'770px',
     '@media(max-width:1124px)':{
        gridTemplateColumns:'100%',
        '@media(max-width:500px)':{
            height:'unset'
        },
    },
    },
    div3:{
        fontFamily:"Open Sans",
        fontStyle:'normal',
        fontWeight:400,
        fontSize:'24px',
        lineHeight:'33px',
        color:'#455582',
        maxWidth:'816px',
        margin:'100px auto',
        textAlign:'center',
        width:"100%",
        '@media(max-width:460px)':{
            fontSize:'18px',
            textAlign:'justify'
        }
    },
    span:{
        margin:'50px 0',
        textAlign:'center'
    },
    star:{
        margin:'10px auto',
        display:'flex',
        '@media(max-width:400px)':{
            transform:"scale(0.8)",
            marginLeft:'-20px'
       
        }
    },
    imgContainer:{
        maxWidth: "1224px" ,
        width: "100%" ,
        margin: '60px auto 0 auto',
        display:"flex" ,
        flexDirection:"row",
        justifyContent:"center",
        columnGap:"43px",
        marginBottom:"40px",
        '@media(max-width:800px)':{
            display:"flex" ,
            flexDirection:"column",
            justifyContent:"center",
            alignItems:"center"
        },
        },
    img4:{
        '@media(max-width:1300px)':{
           transform:'scale(0.8)'
        },
        '@media(max-width:1070px)':{
            // transform:'scale()',
            width:'100%',
            height:'120px'
         },
    },
    button:{
       // height: "97px",
        maxWidth: "380px",
        borderRadius: '20px',
        padding: "20px 50px",
        backgroundColor:"#455582",
        color:"#CCD5E7",
        fontFamily: 'Open Sans',
        fontStyle: "normal",
        fontWeight: "700",
        fontSize: "24px",
        lineHeight: "33px",
        width:"auto",
        margin:"10px 0px",
        border:"none"
        
    },
    box:{
        width:'100%',
        boxShadow:'0px 2px 37px rgba(0, 0, 0, 0.1)',
        background:'#455582',
        minHeight:'600px',
        padding:'20px 20px 10px 70px',
        display:'flex',
        '@media(max-width:1150px)':{
            padding:'20px'
         },
    },
    text:{
        width:'710px',
        fontFamily:"Open Sans",
        fontStyle:'normal',
        fontWeight:400,
        margin:'0 120px',
        fontSize:'24px',
        lineHeight:'33px',
        color:'#fff',
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        '@media(max-width:1500px)':{
            margin:'0 auto'
        },
        '@media(max-width:1320px)':{
            width:'600px',
            textAlign:'justify'
        },
        '@media(max-width:620px)':{
            width:'100%',
           margin:'60px auto',
           '@media(max-width:460px)':{
            margin:'auto',
            fontSize:'18px'
        }
        },
    },
    boxImg:{
        '@media(max-width:1320px)':{
            transform:'scale(0.8)'
        },
        '@media(max-width:1150px)':{
            marginLeft:'-60px',
            transform:'scale(0.69)'
         },
         '@media(max-width:980px)':{
            display:'none'
         },
    },
    box2:{
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        margin:'60px 50px 50px 120px' ,
        '@media(max-width:1320px)':{
           margin:'60px 50px 50px 50px'
        },
        '@media(max-width:1150px)':{
            margin:'60px -30px'
         },
         '@media(max-width:980px)':{
             width:'600px',
            margin:'60px auto'
         },
         '@media(max-width:620px)':{
            width:'100%',
           margin:'60px auto'
        },
    },
    img2:{
        width:'270px',
        //margin:'-20px auto 0 auto',
        
    },
    grid1:{
        background:'#CCD5E7',
        '@media(max-width:1125px)':{
            display:'none'
        },
    },
    amy:{
        margin:'30px 0'
    },
    
    p:{
        fontFamily:'Open Sans',
        fontStyle:'normal',
        fontWeight:400,
        fontSize:'16px',
        lineHeight:'22px',
        background:'#455582',
        color:'#fff', 
        display:'flex',
        flexDirection:'column',
        justifyContent:'center',
        width:'100%',
        '@media(max-width:500px)':{
            display: 'unset',
            fontSize:'18px'
        },
    },
    repo:{
        width:'654px',
        margin:'auto',
        padding:"0px 10px",
        '@media(max-width:720px)':{
            width:'500px',
        },
        '@media(max-width:500px)':{
            width:'90%',
        },
    },
    img3:{
        position:'relative',
        top:'60px',
        left:'300px',
        '@media(max-width:1730px)':{
            left:'200px'
        },
        '@media(max-width:1580px)':{
            left:'100px'
        },
        '@media(max-width:1450px)':{
            left:'50px',
        },

        '@media(max-width:1280px)':{
            left:'-10px'
        },
        '@media(max-width:1190px)':{
            left:'-25px'
        },
    },
    p5:{
        fontFamily:'Open Sans',
        fontStyle:'normal',
        fontWeight:400,
        fontSize:'16px',
        lineHeight:'22px',
        color:'#212121', 
        width:'694px',
        margin:'auto',
        '@media(max-width:700px)':{
            width:'600px'
        },
        '@media(max-width:620px)':{
            width:'500px'
        },
        '@media(max-width:500px)':{
            width:'450px'
        },
        '@media(max-width:440px)':{
            width:'100%'
        },
    },
    div2:{
        display:'flex',
        justifyContent:'center'
    },
    btn:{
        background:'#1B1C33',
        margin:'40px 0',
        borderRadius:'8px',
        padding:'12px 24px',
        outline:'none',
        border:'none',
    },
    btntext:{
        fontFamily:'Open Sans',
        fontStyle:'normal',
        fontWeight:400,
        fontSize:'16px',
        lineHeight:'22px',
        color:'#FFF', 
    },
    div:{
        display:'flex',
        justifyContent:'center'
    },
    divImg:{
       
        '@media(max-width:900px)':{
            transform:'scale(0.6)',
            '@media(max-width:900px)':{
                transform:'scale(1)',
            }
        }
    },
    p1:{
        margin:'60px auto',
        // width:'654px',
    },
    p2:{
        margin:'20px auto',
        // width:'654px',
    },
    p3:{
        margin:'30px auto',
        // width:'654px',
    },
    p4:{
        margin:'20px auto',
        // width:'654px',
    },
});