import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

    body: {
        background:'#fff',
        width:'1016px',
        minHeight:'410px',
        margin:'-10vh auto 0 auto',
        borderRadius:'8px',
        position:'absolute',
        boxShadow:'0px 2px 37px rgba(0, 0, 0, 0.1);',
        '@media(max-width:1046px)':{
            width:'900px',
            position:'static'
        },
        '@media(max-width:1005px)':{
            width:'850px'
        },
        '@media(max-width:892px)':{
            marginTop:'-15vh',
            width:'700px',
            position:'relative',
            zIndex:200
        },
        '@media(max-width:780px)':{
            width:'90%',
        },
    },
    
    head:{
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 700,
        fontSize: '24px',
        lineHeight: '33px',
        color: '#212121',
        textAlign:'center',
        padding:'50px 0 25px 0'
    },
    para1:{
        display:'flex',
        flexDirection:'column',
        fontFamily: 'Open Sans',
        fontStyle: 'normal',
        fontWeight: 400,
        fontSize: '16px',
        lineHeight: '22px',
        color: '#212121',
        width:'814px',
        margin:'20px auto',
        '@media(max-width:892px)':{
            width:'90%',
            textAlign:'justify'
        },
    },
    span1:{
        margin:'10px 0'
    }
})