import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

    box: {
        margin: "40px auto",
        maxWidth: "1240px",
        height: "600px",
        background: "#FFFFFF",
        boxShadow: "0px 2px 37px rgba(0, 0, 0, 0.1)",
        borderRadius: "8px",
        padding: "82px 106px 111px 101px",
        display: "grid",
        gridTemplateColumns: "repeat(2, 1fr)",
        gridGap: "112px",
        alignItems: "center",
        '@media(max-width:1024px)':{
            display:'flex',
            flexDirection:'column-reverse',
            height:'unset'
        },
        '@media(max-width:900px)':{
        padding: "30px",
        }
    },
   
    p: {
        fontSize: "16px",
        lineHeight: "22px",
        color: "#212121",
        width:'100%',
        marginBottom: "25px",
        '@media(max-width:480px)':{
            marginTop:'-30px',
            overflowY:'auto',
            overflowX:'hidden'
        }
    },  
    blue: {
        width: "24px",
        height: "24px",
        background: "#455582",
        marginBottom: "5px",
    },
    h5: {
        fontWeight: "600",
        fontSize: "16px",
        lineHeight: "22px",
        color: "#1B1C33",
        marginBottom: "5px",
    },
    text: {
        fontWeight: "400",
        fontSize: "16px",
        lineHeight: "22px",
        color: "#455582",
    },  
    img:{
        '@media(max-width:480px)':{
            width:'300px',
        }
    }
});