// @flow
import React from "react";
import { css } from "aphrodite"
import { Link } from "react-router-dom";
import styles from "./WebHeaderStyles";
import './WebHeaderStyles.scss'
import { ROUTES } from "../../constants";
import Logo from '../../assets/images/Logo.png'
import Hamburger from "hamburger-react";
export default function WebHeaderView({click,show}) {

  return (
    <div className={css(styles.header)+ " header"}>
      <div className={css(styles.logo)}>
        <img src={Logo} alt="" />
      </div>
      <div className={css(styles.menu)}>
        <div className={css(styles.menulist)}>
          <Link to={ROUTES.HOME} style={{color:"#212121"}}>
            Home
          </Link>
        </div>
        <div className={css(styles.menulist)}>
          <Link to ="/#about" style={{color:"#212121"}}>
            About
          </Link>
        </div>
        <div className={css(styles.menulist)}>
        <Link to ={ROUTES.PODCAST} style={{color:"#212121"}}>
        Podcast
          </Link>
        
        </div>
        <div className={css(styles.menulist)}>
          <Link to={ROUTES.BLOG} style={{color:"#212121"}}>
            Blog
          </Link>
        </div>
        <div className={css(styles.menulist)}>
          <Link to={ROUTES.SUCCESSSTORIES} style={{color:"#212121"}}>
          Success Stories
          </Link>
        </div>
      </div>
      <div className={css(styles.button)}>
        <button className={css(styles.btnlogin)}>
          <Link to ={ROUTES.LOGIN}>
            <span className={css(styles.login)}>Login</span>
          </Link>
        </button>
        <button className={css(styles.btnregister)}>
          <Link to ={ROUTES.REGISTER}>
            <span className={css(styles.register)}>Become a member</span>
          </Link>
        </button>
      </div>

      <div className={css(styles.hamburger)} onClick={()=> click}>
        <Hamburger toggled={show} toggle={click}   />
      </div>

    </div>
  );
}
