// @flow
import React from "react";
import "./BillingStyles.scss";
import Visa from '../../assets/images/Visa.png'
import Paypal from '../../assets/images/Paypal.png'
export default function BillingView() {
  return(
    <div className="billing">
      <div className="firstSection">
      <div className="content">
            <div className="title">Pay with Card</div>
            <img src={Visa}alt="" className="img" />
        </div>
        <div className="content">
            <div className="title">Pay with Paypal</div>
            <img src={Paypal} alt="" className="img2"/>
        </div>
      </div>
        <form action="">
            <div className="form-control">
              <label htmlFor="cardNumber">Card Number</label>
              <input type="text" name="cardNumber" />
            </div>
            <div className="form-control">
              <label htmlFor="cardHolder">Card Holders Name</label>
              <input type="text" name="cardHolder" />
            </div>
            <div className="diff">
              <div className="form-control col_1">
                <label htmlFor="cardExpiry">Card Expiry</label>
                <input type="text" name="cardExpiry" />
              </div>
              <div className="form-control col_2">
                <label htmlFor="cvv">CVV</label>
                <input type="text" name="cvv"/>
              </div>
            </div>
            <button type="submit"><span className="text">Make Payment</span></button>
        </form>
    </div>
  );
}
