import { StyleSheet } from "aphrodite";

export default StyleSheet.create({
    header: {
        display: 'flex',
        justifyContent: 'space-between',
        padding:"10px 50px",
        '@media(max-width:1158px)':{
            padding:'10px 0'
        }
    },
    logo:{
        //width:'16%'
    },
    menu: {
        display: 'flex',
        alignItems:'center',
        '@media(max-width:1050px)':{
            display:'none'
        }
    },
    menulist: {
        margin: '0 20px',
        fontFamily:'Open Sans',
        fontStyle: 'normal',
        fontWeight: 600,
        fontSize: '16px',
        lineHeight: '21px',
        color:'#212121',
    },
    button: {
        display: 'flex',
        alignItems:'center',

        '@media(max-width:1050px)':{
            display:'none'
        }
    },
    hamburger:{
        display:'none',
        flexDirection:'column',
        justifyContent:'space-between',
        alignItems:'center',
        cursor:'pointer',
        transform:'scale(1.4em)',
        '@media(max-width:1050px)':{
            display:'flex'
        }
    },
    btnlogin: {
      outline:'none',
      background:'transparent',
      border: 'none',
      margin:'0 5px',
      padding:'20px 60px',
      borderRadius:'8px',
      color:'#212121'
    },
    btnregister: {
      outline:'none',
      background:'#1B1C33',
      border: 'none',
      padding:'15px 88px',
      color:"#fff",
      margin:'0 100px 0 5px',
      borderRadius:'8px',
    },
    login: {
        fontFamily:'Open Sans',
        fontStyle: 'normal',
        fontWeight: 600,
        fontSize: '16px',
        lineHeight: '22px',
        color:"#212121"
    },
    register: {
        fontFamily:'Open Sans',
        fontStyle: 'normal',
        fontWeight: 600,
        fontSize: '16px',
        lineHeight: '22px',
        color:"#ffffff"
    },


});