import { StyleSheet } from "aphrodite";

export default StyleSheet.create({

title:{
    fontFamily:"PlayFair Display",
    fontStyle:'normal',
    fontSize:'64px',
    lineHeight:'85px',
    color:'#1B1C33',
    textAlign:'center',
    '@media(max-width:1034px)':{
        fontSize:'46px'
    },
    '@media(max-width:860px)':{
        fontSize:'40px',
        marginTop:'-30px'
    },
    '@media(max-width:680px)':{
        fontSize:'24px',    
        lineHeight:'55px',
    },
    '@media(max-width:500px)':{
        fontSize:'24px',    
        lineHeight:'22px',
        width:'100%'
    },
} ,

button:{
    // height: "97px",
    maxWidth: "380px",
    borderRadius: '20px',
    padding: "20px 50px",
    backgroundColor:"#455582",
    color:"#CCD5E7",
    fontFamily: 'Open Sans',
    fontStyle: "normal",
    fontWeight: "700",
    fontSize: "24px",
    lineHeight: "33px",
    width:"auto",
    margin:"10px 0px",
    border:"none"
    
},
subtext:{
    fontFamily:"Open Sans",
    fontStyle:'normal',
    fontWeight:400,
    fontSize:'16px',
    lineHeight:'22px',
    textAlign:"center",
    color:'#212121',
    margin:'20px auto',
    width:'860px',
    '@media(max-width:1034px)':{
        width:'700px',
    },
    '@media(max-width:860px)':{
        width:'580px',
    },
    '@media(max-width:700px)':{
        fontSize:'14px',
        width:'500px',
    },
    '@media(max-width:600px)':{
        fontSize:'14px',
        width:'450px'
    },
    '@media(max-width:500px)':{
        fontSize:'18px',
        lineHeight:'22px',
        width:'100%'
    },
    
},
imgContainer:{
    maxWidth: "1224px" ,
        width: "100%" ,
        margin: '60px auto 0 auto',
        display:"flex" ,
        flexDirection:"row",
        justifyContent:"center",
        columnGap:"43px",
        marginBottom:"40px",
        '@media(max-width:800px)':{
            display:"flex" ,
            flexDirection:"column",
            justifyContent:"center",
            alignItems:"center"
        },
    },
img:{
    '@media(max-width:1300px)':{
       transform:'scale(0.8)'
    },
    '@media(max-width:1070px)':{
        // transform:'scale()',
        width:'100%',
        height:'120px'
     },
}


});