// @flow
import React, { Component } from 'react'
import {css} from "aphrodite"
import styles from "./Styles"


export class index extends Component {
   bgStyle = {
    minHeight:this.props.height ? this.props.height :  '100vh',
    background:this.props.bg ? this.props.bg : '#CCD5E7'
  }
 
  render(props) {
    return (
        <div className={css(styles.bg)} style={this.bgStyle}>
            {this.props.children}
      </div>
    )
  }
}

export default index

